.dropdown {
    position: relative;
    display: inline-block;
  }

.dropdown-content {
    display: none;
    position: absolute;
    right:0;
    background-color: #f9f9f9;
    width:12rem;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    text-align: center;
}

.dropdown-link{
    text-decoration: none;
    display: block;
    padding-right: 30px;
    color: black;
    font-size: 20px;
    margin:15px 0px;
}
.dropdown:hover .dropdown-content {
    display: block;
}

@media only screen and (max-width: 900px){
