
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto&display=swap');


@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Playfair+Display:ital,wght@0,400;0,500;0,700;0,800;0,900;1,500&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap');

*{
    margin:0;
    padding:0;
}

.view_image_div{
    
    width:100%;
    margin: auto;
    height:60vh;
    border-radius: 5px;
}
.view_image_div img{
    width: 100%;
    height:100%;
    object-fit: cover;
    opacity:1;
    border-radius: 5px;
}

.about_blog{
    width: 98%;
    display: flex;
    flex-direction: row;
    justify-content:space-between ;
    align-items: center;
    
    /* background-color: red; */
    margin:20px auto;
}

.about_blog_1{
    /* background-color: green; */
    width: 50%;
    overflow: hidden;
    text-align: left;
    color:#5c5c5c;
}

.about_blog_2{
    /* background-color: yellow; */
    width: 50%;
    overflow: hidden;
    text-align: right;
    color:#5c5c5c;
}

.whole_blog{
    word-wrap:break-word;
    /* background-color: red; */
    text-align: center;
    padding:0px 30px 0px 30px;
}

.blog_title{
    color:#383838;
    font-family: 'Playfair Display', serif;
}

.blog_body{
    text-align: justify;

    font-size: large;
    color:#5c5c5c;
    font-family: 'Poppins', sans-serif;
    word-spacing: 6px;
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;
}

.blog_controls{
    width:auto;
    /* background-color: orange; */
    float: right;
    margin-top: 10px;
}
.fa-edit{
    color:rgb(27, 121, 245);
    font-size: 2.5rem;
    margin: 0px 8px 0px 8px;
    cursor: pointer;
}
.fa-trash-alt{
    color: red;
    font-size: 2.5rem;
    margin: 0px 8px 0px 8px;
    cursor: pointer;
}
.comment-outer{
    /* background-color: red; */
    padding:10px 50px;
   
    display: flex;
    flex-direction: row;
}
.comment-inner1{
    /* background-color: chartreuse; */
    width:55px;
    min-width: 55px;
    height:55px;
    min-height: 55px;
    border-radius: 50%;

}
.comment-inner1 img{
    width:100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}

.comment-inner2{
    /* background-color: green; */
    padding:0px 20px;
    word-break: break-all;
}

.user-text{
    color:#878787;
    font-size:25px;
    margin-bottom: 0px;
}

.user-text:hover{
    color:blue;
}

.delete_comment{
    background-color: red;
    text-decoration: none;
    border: none;
    padding:5px;
    border-radius: 5px;
    color:white;
    cursor: pointer;
}
.delete_comment:hover{
    background-color: rgb(180, 11, 11);
}

.post-comment{
    width:90%;
    margin-left: 20px;
    border:none;
    border-bottom: 2px solid #5C5C5C;
    font-size: 25px;
    outline: none;
}
.comment-inner2 p{
    font-size:28px;
}

@media only screen and (max-width: 850px){

    .whole_blog{
        padding:0px 10px 0px 10px;
    }
}

@media only screen and (max-width: 700px){

    .about_blog{
        flex-direction: column;
        margin:20px 8px;
    }
    .about_blog_1{
        width:100%;
    }
    .about_blog_2{
        width:100%;
        text-align: left;
    }

    .blog_body{
        text-align: justify;
        font-size: large;
        color:#5c5c5c;
        font-family:  sans-serif;
        word-spacing: 0px;
        
    }
    .fa-edit{
        font-size: 2rem;
        margin: 0px 5px 0px 5px;
    }
    .fa-trash-alt{
        font-size: 2rem;
        margin: 0px 5px 0px 5px;
    }
    .comment-outer{
        padding:10px 0px;
    }
}